ApagarLista(){

this.blocklista = true;

let alert = this.alertCtrl.create({
    title: this.nomedaslistas.nomelista,
    message: 'voc deseja limpar o contedo da lista?',
    buttons: [
      {
        text: 'Cancelar',
        role: 'cancel',
        handler: () => {
          this.blocklista = false;
        }
      },
      {
        text: 'Confirmar',
        handler: () => {
            this.blocklista = false;

            for(var i=0;i<=this.produtosVerduras.length;i++){
            this.produtosVerduras.splice(i);
          }
            for(var i=0;i<=this.produtosFrutas.length;i++){
            this.produtosFrutas.splice(i);
          } 
            for(var i=0;i<=this.produtosBebidas.length;i++){
            this.produtosBebidas.splice(i);
          }
            for(var i=0;i<=this.produtosHigiene.length;i++){
            this.produtosHigiene.splice(i);
          } 
            this. ApgLista = false;
            this.lista = false;
            this.menulistas = true;
                  }
                }
              ]
            });
  alert.present();
}

